:root {
  --background: #edf3f7;
  --foreground: #17212b;
  --panel: rgba(255, 255, 255, 0.82);
  --panel-border: rgba(23, 33, 43, 0.12);
  --accent: #0d7666;
  --accent-strong: #0b4358;
  --muted: #5f6c7a;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: "Pretendard Variable", "SUIT Variable", "Noto Sans KR", sans-serif;
  min-height: 100vh;
  background-image: radial-gradient(circle at 8% -8%, rgba(87, 164, 150, 0.24) 0%, transparent 36%),
    radial-gradient(circle at 92% 6%, rgba(95, 146, 212, 0.2) 0%, transparent 36%),
    linear-gradient(160deg, #f8fbfe 0%, #e8f1f7 100%);
}
body::before {
  content: "";
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: -1;
  background-image: linear-gradient(
      rgba(23, 33, 43, 0.03) 1px,
      transparent 1px
    ),
    linear-gradient(90deg, rgba(23, 33, 43, 0.03) 1px, transparent 1px);
  background-size: 36px 36px;
  opacity: 0.12;
}
.ui-shell {
  min-height: 100vh;
  color: var(--foreground);
}
.ui-header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.55);
  background: rgba(243, 248, 252, 0.74);
  backdrop-filter: blur(14px);
}
.ui-panel {
  border: 1px solid var(--panel-border);
  background: var(--panel);
  backdrop-filter: blur(10px);
  box-shadow: 0 22px 50px -34px rgba(11, 67, 88, 0.6),
    0 8px 24px -18px rgba(0, 0, 0, 0.25);
}
.ui-tab {
  border-radius: 999px;
  padding: 0.48rem 1rem;
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--muted);
  transition: all 160ms ease;
}
.ui-tab:hover {
  background: rgba(13, 118, 102, 0.12);
  color: #0d4a4f;
}
.ui-tab-active {
  background: linear-gradient(120deg, var(--accent) 0%, var(--accent-strong) 100%);
  color: #f7fbff;
  box-shadow: 0 10px 26px -12px rgba(11, 67, 88, 0.9);
}
.ui-appear {
  animation: rise-in 420ms ease-out both;
}
.ui-hero {
  border: 1px solid rgba(255, 255, 255, 0.55);
  background: linear-gradient(140deg, rgba(7, 97, 118, 0.92) 0%, rgba(15, 141, 125, 0.88) 52%, rgba(244, 196, 141, 0.76) 100%);
  box-shadow: 0 28px 55px -34px rgba(10, 66, 73, 0.95);
}
.ui-chip {
  border-radius: 999px;
  border: 1px solid rgba(23, 33, 43, 0.12);
  background: rgba(255, 255, 255, 0.72);
}
.ui-chip-active {
  border-color: rgba(11, 67, 88, 0.35);
  background: linear-gradient(120deg, rgba(13, 118, 102, 0.16) 0%, rgba(11, 67, 88, 0.16) 100%);
}
@keyframes rise-in {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
